
























































































.gallery {
    .fullscreen {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        z-index: 100;

        svg {
            fill: var(--primary);
        }

        .actions,
        .footer {
            height: 2.5rem;
            display: flex;
            justify-content: flex-end;
        }

        .slider {
            flex: 1;
            display: flex;
            align-items: center;
            width: 100%;
            overflow-y: auto;
            scroll-snap-type: x mandatory;

            img {
                scroll-snap-align: center;
                width: 100%;
            }
        }

        .footer {
            padding: 0.5rem;

            .slash {
                color: var(--secondary);
            }

            span:not(:last-child) {
                margin-right: 0.5rem;
            }
        }
    }

    .grid {
        .gallery-item {
            border: 2px solid var(--secondary);
            display: block;
            width: 100%;

            &:not(:last-child) {
                margin-bottom: 1rem;
            }
        }
    }

    .hint {
        margin-top: 0.5rem;
        text-align: right;
        font-size: 0.75rem;
    }
}
