@use "sass:color";

$themes: (
    'light': (
        'colors': (
            'primary': #000000,
            'on-primary': #ffffff,
            'secondary': color.mix(#ffffff, #000000, 90%),
        ),
        'surfaces': (
            '0': (
                'background-color': #ffffff,
            ),
            '1': (
                'background-color': color.mix(#ffffff, #000000, 90%),
            ),
        ),
    ),
    'dark': (
        'colors': (
            'primary': #ffd740,
            'on-primary': #000000,
            'secondary': color.mix(#000000, #ffd740, 75%),
        ),
        'surfaces': (
            '0': (
                'background-color': #000000,
            ),
            '1': (
                'background-color': color.mix(#000000, #ffd740, 75%),
            ),
        ),
    ),
);

* {
    box-sizing: border-box;
}

a,
a:active,
a:focus,
a:visited {
    color: unset;
    text-decoration: unset;
}

p, h1, h2, h3, h4, h5, h6 {
    margin: 0 0 0.5rem 0;

    &:last-child {
        margin-bottom: 0;
    }
}

h1 {
    font-size: 1rem;
    font-weight: 400;
}
