























@use "src/styles/constants" as c;

.tabs {
    display: flex;

    .tab {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        color: inherit;
        padding: 0.5rem;
        height: c.$height;

        &:not(:last-child) {
            border-right: c.$border;
        }

        &[selected] {
            font-weight: 700;
            background-color: var(--secondary);
        }
    }
}
