


















































































@use 'src/styles/constants' as c;

.posts {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    height: 100%;

    .title-bar {
        form {
            flex: 1;
            display: flex;
            height: c.$height;
        }

        .search {
            flex: 1;
            height: c.$height;
        }

        .btn,
        .region-picker {
            border-left: c.$border;
            min-width: c.$height;
        }
    }

    .tabs {
        border-bottom: c.$border;
    }

    main {
        flex: 1;
        overflow-y: auto;
        position: relative;

        .post {
            margin: 1.5rem 1rem;

            h1 {
                margin: 0.5rem 0;
            }
        }
    }
}
