








































































.region-picker {
    .btn,
    .form,
    .textfield {
        height: 100%;
    }

    .btn {
        &:not(.square) {
            padding-left: 0.75rem;
            padding-right: 0.75rem;
        }

        svg {
            fill: var(--primary);
        }
    }

    .form {
        width: clamp(10rem, 50vw, 20rem);
        display: flex;
        transition: max-width 2s ease-in-out;

        .textfield {
            flex: 1;

            &::placeholder {
                color: var(--primary);
                opacity: 0.54;
            }
        }
    }
}
