






















































































.post {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.title-bar {
    .view-on-reddit {
        margin-left: auto;
        border-left: 1px solid var(--primary);
    }
}

main {
    flex: 1;
    overflow-y: auto;
    padding: 1rem 0;

    .author {
        margin: 0 1rem 1.5rem 1rem;
    }

    section {
        margin: 1rem 1rem 1.5rem 1rem;
        border: 1px solid var(--primary);
        padding: 1.25rem 1rem 1rem 1rem;
        position: relative;

        &:last-child {
            margin-bottom: 0;
        }

        .title {
            position: absolute;
            padding: 0 0.5rem;
            top: 0;
            transform: translate(-0.5rem, -50%);
        }

        p {
            margin: 0;

            &:not(:last-child) {
                margin-bottom: 1rem;
            }
        }

        .payment-methods {
            display: flex;
            flex-wrap: wrap;

            .payment-method:not(:last-child) {
                margin-right: 0.5rem;
            }
        }
    }

    .description {
        padding: 0;

        h1 {
            left: 1rem;
        }

        .markdown {
            padding: 1.25rem 1rem 1rem 1rem;
            overflow-y: auto;
        }
    }
}
