



















@use 'src/styles/constants' as c;

.title-bar {
    border-bottom: c.$border;
    display: flex;
    height: c.$height;

    svg {
        fill: var(--primary);
    }

    .slot {
        flex: 1;
        display: flex;

        > * {
            height: c.$height;
        }
    }

    .theme-picker {
        border-left: c.$border;
    }
}
